@import url(https://fonts.googleapis.com/css?family=Tangerine&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header h2 {
  font-size: calc(40px + 4vmin);
  line-height: calc(40px + 2vmin);
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
  color: #fff6a9;
  font-family: "Tangerine", serif;
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form-group{
  font-family: serif;
  display: block;
  text-align: center;
  font-size: calc(35px + 2vmin);
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding-bottom:calc(30px + 2vmin);
  padding-top:  calc(30px + 2vmin);
}

.form-control{
  font-size: calc(35px + 2vmin);
}

.form-group button {
  font-size: calc(35px + 2vmin);
  position: relative;
  z-index: 2;
  background-color: #333;
  border: 2px solid #333;
  color: #fff;
  line-height: 50px;
}
.form-group button:hover {
  background-color: #fff;
  border-color: #59b1eb;
  color: #59b1eb;
}
.form-group button::before,
.form-group button::after {
  top: 0;
  width: 50%;
  height: 100%;
  background-color: #333;
}
.form-group button::before {
  right: 0;
}
.form-group button::after {
  left: 0;
}
.form-group button:hover::before,
.form-group button:hover::after {
  width: 0;
  background-color: #59b1eb;
}


.weather-data {
  text-align: center;
  align-items: center;
  font-size: calc(18px + 5vmin);
  width: 73%;
  margin: 0 auto;
  border-collapse: collapse;
  border-collapse:separate;
  border-spacing: 0;
}

.weather-data thead th {
  color: #FFF; 
  font-weight: bold;
  background:linear-gradient(#99d5dd,#00BCD4);
  padding:10px 0;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0px -3px 8px -1px #eee inset;
}

.city{
  border-radius: 10px;
  text-align: center;
  color:white;
  background: linear-gradient(#86ace2,#0065f4);
  border-left: 0.3px solid #ffffff;
  padding: 10px 0;
}

.weather-data tbody tr{
  text-align: center;
  border-left: 1px solid #c3c3c3;
  border-bottom: 1px solid #c3c3c3;
  border-top:none;
  box-shadow: 0px -3px 8px 1px #eee inset;
  padding: 12px 0;
  background: white;
  width: 100%;
  height: 100%;
}

.weather{
  background:#f6efdb;
  width: 100%;
  height: 100%;
}

